body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}


.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #223338;
}
.square {
    width: 100px;
    height: 100px;
    background-color: #848484;
    margin: 5px;
    transition: all .2s;
    border-radius: 0%;
    -webkit-clip-path: polygon(36% 0, 0 0, 0 29%, 0 57%, 0 100%, 30% 100%, 73% 100%, 100% 100%, 100% 59%, 100% 31%, 100% 0, 74% 0);
            clip-path: polygon(36% 0, 0 0, 0 29%, 0 57%, 0 100%, 30% 100%, 73% 100%, 100% 100%, 100% 59%, 100% 31%, 100% 0, 74% 0);
}

.square--X {
    background-color: #CD493A;
    -webkit-clip-path: polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%);
            clip-path: polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%);
}

.square--O {
    background-color: #4392F1;
    border-radius: 50%;
}

.square--X.winner {
    -webkit-animation: x-winner-animation infinite 2s ease-in-out;
            animation: x-winner-animation infinite 2s ease-in-out;
}

.square--O.winner {
    -webkit-animation: o-winner-animation infinite .5s ease-in-out;
            animation: o-winner-animation infinite .5s ease-in-out;
}

@-webkit-keyframes x-winner-animation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes x-winner-animation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes o-winner-animation {
    0% { border-radius: 50%;}
    50% { border-radius: 0%;}
    100% { border-radius: 50%;}
}

@keyframes o-winner-animation {
    0% { border-radius: 50%;}
    50% { border-radius: 0%;}
    100% { border-radius: 50%;}
}
.board{
    display: flex;
    flex-direction: column;
}
.row{
    display: flex;
}
.score-board {
    width: 320px;
    display: grid;
    grid-auto-flow: column;
    margin-top: 5px;
}

.score-board div {
    color: white;
    text-align: center;
    font-size: 2em;
    padding: 5px;
    font-weight: bold;
    background-color: #4392F1;
}

.score-board div:first-child {
    background-color: #CD493A;
}
